@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&family=Open+Sans:ital,wght@0,400;0,600;1,300&family=Roboto:wght@400;700&family=Tangerine&display=swap);
:root {
  --preto: #000000;
  --azul: #14213D;
  --vermelho: #A22C29;
  --cinza: #D0CCD0;
  --branco: #FFFFFF;
  --sans: 'Open-Sans', sans;
  --dancing: 'Dancing-Script', cursive;
  --tangerine: 'Tangerine', cursive;
}

body {
  padding: 0;
  margin: 0;
  background: #000000;
  background: var(--preto);
}
